import { Avatar, Box, Button, TextField } from "@material-ui/core";
import React from "react";
import AssignmentIcon from "@material-ui/icons/Assignment"

const Login = () => {
  return (
    <div>
        <Box
            display='flex'
            alignItems='center'
            justifyContent='center'
            height='100vh'
            flexDirection='column'>
            <Box
                flexDirection='column'
                display='flex'
                alignItems='center'>
                <Box
                    fontSize='500%'
                    letterSpacing='10px'
                    fontStyle='italic'
                    display='flex'
                    flexDirection='row'>
                    <Avatar
                        variant='rounded'
                        style= {{ 
                            backgroundColor: '#8bc34a',
                            height:'20%',
                            width: '6%'
                        }}>
                        <AssignmentIcon />
                    </Avatar>
                    Lessons
                </Box>
                <Box 
                padding={2}
                height='25%'
                width='85%'>
                    <Box>
                        <TextField 
                            label='username'
                            autoFocus
                            required 
                            fullWidth
                        />
                    </Box>
                    <Box>
                        <TextField 
                            label='password' 
                            type='password'
                            required
                            fullWidth
                        />
                    </Box>
                    <Button variant='contained' >Log in</Button>
                </Box>
            </Box>
        </Box>
    </div>
    
    
  );
}

export default Login;
